.header {
   width: 680px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.App-logo {
    height: 100px;
    pointer-events: none;
}
.App-logo1 {
    height: 100px;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 30s linear;
    }
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo1 {
        animation: App-logo1-spin infinite 30s linear;
    }
}
@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
@keyframes App-logo1-spin {
    from {
        transform: rotate(360deg);
    }
    to {
        transform: rotate(0deg);
    }
}
